export class Storage {
  seriesKey = "current_series"
  pokemonKey = "current_pokemon"
  selectedCardsKey = "desired_cards"
  downloadname = "desired_cards"
  constructor(){}

  get series() {
    return localStorage.getItem(this.seriesKey) || "sv09"
  }
  
  set series(seriesValue : string) {
    localStorage.setItem(this.seriesKey, seriesValue)
  }

  get pokemonIdx() {
    return localStorage.getItem(this.pokemonKey) || "1"
  }

  set pokemonIdx(pokemonIdxValue: string) {
    localStorage.setItem(this.pokemonKey, pokemonIdxValue)
  }

  get selectedCards(): string[] {
    return (localStorage.getItem(this.selectedCardsKey)?.split(",") || []).filter((i) => i)
  }

  set selectedCards(cardsValue: string[] | string) {
    if (typeof(cardsValue) == "string") {
      const cards = this.selectedCards
      localStorage.setItem(
        this.selectedCardsKey,
        cards.concat(cardsValue.replaceAll("\n", ",").split(",")).join(","),
      );
    } else {
      localStorage.setItem(
        this.selectedCardsKey,
        cardsValue.join(","),
      );
    }
  }

  clear(key: string) {
    localStorage.removeItem(key)
  }

  get downloadFilename() {
    return `${this.downloadname}_${new Date().toISOString()}.csv`
  }
}