import { createContext, JSX, useEffect, useState, memo, useRef } from "react";
import { TextField, Autocomplete } from "@mui/material";
import { SelectedCountProvider, useSelectedCount } from "./SelectedCountContext";
import { Storage } from "./Storage";
import {POKEBALL_SVG} from "./PokeballSVG"

interface SeriesHash {
  [key: string]: string;
}

interface SeriesHashType {
  [key: string]: { key: string; title: string }[];
}

interface AllPokemonType {
  name: string;
  pokedex: string;
}

interface PokemonType {
  label: string;
  name: string;
  pokedex: string;
}

interface CardType {
  link: string;
  image: string;
  label: string;
  series: string;
  card_id: string;
  pokedex: string;
  active?: boolean;
}

let globalSeries: SeriesHash;
let globalSeriesHash: SeriesHashType;
let activeCards: string[] = [];
let AllPokemon: AllPokemonType[] = [];
let AllCards: { [key: string]: CardType[] } = {};

const PokeStorage = new Storage()

const getCurrentSeries = () => PokeStorage.series;
const getCurrentPokemon = () => PokeStorage.pokemonIdx;
const scrollToTop = () => {
  const displayContainer = document.getElementById("displayContainer");
  if (displayContainer) {
    displayContainer.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }
};

const hasDesiredCard = (card: CardType) => {
  return PokeStorage.selectedCards.includes(card.card_id);
};

const getDesiredCards = () => PokeStorage.selectedCards;

const Card = memo(function Card({
  card,
}: {
  card: CardType;
}) {
  const { setSelectedCount } = useSelectedCount();
  const [cardActive, setCardActive] = useState(hasDesiredCard(card));

  const image =
    "https://assets.pokemon.com/static-assets/content-assets/cms2/img/cards/web" +
    card.image;
  const link =
    "https://www.pokemon.com/us/pokemon-tcg/pokemon-cards/series" + card.link;

  const storeCard = (active: boolean) => {
    let current_cards = PokeStorage.selectedCards;
    if (active) {
      current_cards.push(card.card_id);
    } else {
      current_cards = current_cards.filter((i) => i != card.card_id);
    }
    if (current_cards) {
      activeCards = current_cards;
      PokeStorage.selectedCards = current_cards;
    } else {
      activeCards = [];
      PokeStorage.clear("desired_cards")
    }
  };

  const onCardClick = (ev: React.MouseEvent) => {
    if (ev?.ctrlKey) {
      open(link);
    } else {
      storeCard(!cardActive);
      setCardActive(!cardActive);
      setSelectedCount(() => activeCards.length);
    }
  };

  let series;
  try {
    series = globalSeries[card.series] || "Unknown";
  } catch {}

  return (
    <div className={cardActive ? "card active" : "card"} onClick={onCardClick}>
      <div>
        <img src={image} />
      </div>
      <div className="card-name">
        {card.label} - {card.card_id}
      </div>
      <div className="card-name">{series}</div>
    </div>
  );
})

function Cards({
  title,
  cards,
}: {
  title: string;
  cards: CardType[];
}) {
  const cardIdx = (cards || [])[0]?.pokedex?.padStart(4, "0");
  const cardsDisplay = cards.map((card) => {
    const id = card.card_id || String(Math.random());
    return (
      <Card card={card} key={id} />
    );
  });
  return (
    <div className="cards-section">
      <h3>
        #{cardIdx} {title}
      </h3>
      <div className="cards-container">{cardsDisplay}</div>
    </div>
  );
}

function PokemonDisplay({
  pokedata,
}: {
  pokedata: { [key: string]: CardType[] };
}) {
  const pokerowsEle = Object.keys(pokedata).map((title) => {
    const id = String(Math.random());
    return (
      <Cards
        title={title}
        cards={pokedata[title]}
        key={id}
      />
    );
  });
  return (
    <div className="display-container" id="displayContainer">
      {pokerowsEle}
    </div>
  );
}

function SelectedCount() {
  const {selectedCount} = useSelectedCount();
  return (
    <a className="selected-cards" id="selectedCards" href="?select_type=selected">
      {selectedCount}
    </a>
  );
}
function ExportFile() {
  const generateCSV = (data: string[], filename: string) => {
    // Convert the data array into a CSV string
    const csvContent = data.join("\n");

    // Create a Blob from the CSV string
    const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });

    // Create a link element
    const link = document.createElement("a");

    // Create a URL for the Blob and set it as the href attribute
    const url = URL.createObjectURL(blob);
    link.setAttribute("href", url);

    // Set the download attribute with the desired file name
    link.setAttribute("download", filename);

    // Append the link to the document body and trigger a click
    document.body.appendChild(link);
    link.click();

    // Clean up by removing the link and revoking the Blob URL
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  const onDownloadCSV = (desiredCards: string[]) => {
    if (desiredCards.length > 0) {
      generateCSV(
        desiredCards,
        PokeStorage.downloadFilename,
      );
    } else {
      alert("No cards selected to export.");
    }
  };

  const copyTextToClipboard = (text: string) => {
    if (text) {
      navigator.clipboard.writeText(text).then(
        () => {
          console.log("Text copied to clipboard:", text);
          alert("The cards have been copied to your clipboard");
        },
        (err) => {
          console.error("Failed to copy text: ", err);
        },
      );
    } else {
      alert("You have no cards selected");
    }
  };

  const onExport = (ev: React.MouseEvent) => {
    const text = localStorage.getItem("desired_cards");
    const isMobile = /Mobi|Android/i.test(navigator.userAgent);

    if (text) {
      if (isMobile) {
        copyTextToClipboard(text);
      } else {
        onDownloadCSV(text.split(","));
      }
    } else {
      alert("You have no cards selected");
    }
  };

  return (
    <button onClick={(e) => onExport(e)} className="export">
      Export
    </button>
  );
}
function ImportFile({
  setIsLoaded,
}: {
  setIsLoaded: React.Dispatch<React.SetStateAction<boolean>>;
}) {
  const handleFileUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]; // Get the first uploaded file
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const content = String(e.target?.result); // File content as a string
        PokeStorage.selectedCards = content;
        console.log("File content:", content?.length || 0);
        setIsLoaded(false);
        setTimeout(() => {
          setIsLoaded(true);
        }, 100);
      };
      reader.readAsText(file); // Read the file as text
    }
  };

  return <input type="file" onChange={handleFileUpload} />;
}

function PokemonFilter({
  pokemon,
  setPokemon,
}: {
  pokemon: PokemonType | null;
  setPokemon: React.Dispatch<React.SetStateAction<PokemonType | null>>;
}) {
  const [inputValue, setInputValue] = useState("");
  const options = AllPokemon.map((r) => {
    return {
      ...r,
      label: `#${r.pokedex.padStart(4, "0")} ${r.name}`,
    };
  });

  return (
    <div className="flex">
      <Autocomplete
        disablePortal
        value={pokemon}
        onChange={(event, pokemon) => {
          setPokemon(pokemon);
          localStorage.setItem("current_pokemon", pokemon?.pokedex || "1");
        }}
        inputValue={inputValue}
        onInputChange={(event, newInputValue) => {
          setInputValue(newInputValue);
        }}
        options={options}
        sx={{ width: 300 }}
        renderInput={(params) => <TextField {...params} label="Pokemon" />}
      />
      <a className="series-link" href="?select_type=series">
        <div className="series-card">{POKEBALL_SVG}</div>
      </a>
    </div>
  );
}

function SeriesFilter({
  seriesName,
  setSeriesName,
}: {
  seriesName: string;
  setSeriesName: React.Dispatch<React.SetStateAction<string>>;
}) {
  const onChange = (e: React.ChangeEvent<HTMLSelectElement>) => {
    const seriesN = e?.target?.value
    if (seriesN) {
      setSeriesName(seriesN);
      localStorage.setItem("current_series", seriesN);
    }
  };
  return (
    <div className="flex">
      <select
        value={seriesName}
        onChange={onChange}
      >
        {Object.keys(globalSeriesHash).map((group) => {
          return (
            <optgroup label={group} key={group}>
              {globalSeriesHash[group].map((i) => (
                <option value={i.key} key={i.key}>
                  {i.title}
                </option>
              ))}
            </optgroup>
          );
        })}
      </select>
      <a className="pokemon-link" href="?select_type=pokemon">
        {POKEBALL_SVG}
      </a>
    </div>
  );
}

function Filter({
  filterType,
  setPokedata,
}: {
  filterType: string | null;
  setPokedata: React.Dispatch<
    React.SetStateAction<{ [key: string]: CardType[] }>
  >;
}) {
  const [pokemon, setPokemon] = useState<PokemonType | null>(null);
  const [seriesName, setSeriesName] = useState<string>(getCurrentSeries());
  const onlyActiveCards = false;

  // const isInitialRender = useRef(true);

  type FilterFunction = (input1: CardType) => boolean;
  const reduceCards = (filterFn: FilterFunction) => {
    return Object.keys(AllCards).reduce(
      (memo: { [key: string]: CardType[] }, k) => {
        const pokeCards = AllCards[k];
        const filteredCards = pokeCards.filter(card => {
          if (onlyActiveCards) return activeCards.includes(card.card_id);
          return true;
        }).filter(filterFn);
        if (filteredCards.length > 0) memo[k] = filteredCards;
        return memo;
      },
      {},
    );
  };

  useEffect(() => {
    scrollToTop()
    if (filterType == "pokemon") {
      const reducedData = reduceCards(
        (c) => c.pokedex == String(pokemon?.pokedex || getCurrentPokemon()),
      );
      setPokedata(reducedData);
    } else if (filterType == "series") {
      const reducedData = reduceCards((c) => c.series == seriesName);
      setPokedata(reducedData);
    } else if (filterType == "selected") {
      const selectedCards = PokeStorage.selectedCards;
      const reducedData = reduceCards((c) => selectedCards.includes(c.card_id));
      setPokedata(reducedData);
    }
  }, [pokemon, seriesName]);

  if (filterType == "pokemon") {
    return <PokemonFilter pokemon={pokemon} setPokemon={setPokemon} />;
  } else if(filterType == "series") {
    return (
      <SeriesFilter seriesName={seriesName} setSeriesName={setSeriesName} />
    );
  } else if(filterType == "selected") {
    return (
      <div className="filterless-container">
        <span className="filter-title">Selected Cards</span>
        <a className="series-link" href="?select_type=series">
          <div className="series-card">{POKEBALL_SVG}</div>
        </a>
      </div>
    )
  } else {
    return (
      <div className="filterless-container">
        <span className="filter-title">BAD FILTER</span>
        <a className="series-link" href="?select_type=series">
          <div className="series-card">{POKEBALL_SVG}</div>
        </a>
      </div>
    )
  }
}

function HeaderBar({
  filterType,
  setPokedata,
  setIsLoaded,
}: {
  filterType: string;
  setPokedata: React.Dispatch<
    React.SetStateAction<{ [key: string]: CardType[] }>
  >;
  setIsLoaded: React.Dispatch<React.SetStateAction<boolean>>;
}) {
  const { setSelectedCount } = useSelectedCount();
  useEffect(() => {
    activeCards = getDesiredCards();
    setSelectedCount(() => activeCards.length);
  })
  
  return (
    <div className="header-row">
      <Filter filterType={filterType} setPokedata={setPokedata} />
      <div>
        <ImportFile setIsLoaded={setIsLoaded} />
        <ExportFile />
        <SelectedCount />
      </div>
    </div>
  );
}

function ScrollToTop() {
  return (
    <div className="go-to-top-button">
      <button onClick={(e) => scrollToTop()}>Top</button>
    </div>
  );
}

export default function Base({ selectType }: { selectType: string }) {
  const [isLoaded, setIsLoaded] = useState(false);

  const [pokedata, setPokedata] = useState<{ [key: string]: CardType[] }>({});

  useEffect(() => {
    const cardsPromise = fetch(`cards.json`)
      .then((resp) => resp.json())
      .then((data: { [key: string]: CardType[] }) => {
        AllCards = data;
      });
    const pokemonPromise = fetch(`pokemon.json`)
      .then((resp) => resp.json())
      .then((data: AllPokemonType[]) => {
        AllPokemon = data;
      });
    const seriesPromise = fetch(`series.json`)
      .then((resp) => resp.json())
      .then((data: SeriesHashType) => {
        globalSeries = Object.fromEntries(
          Object.values(data).flatMap((i) => i.map((ii) => [ii.key, ii.title])),
        );
        globalSeriesHash = data;
      });
    Promise.all([cardsPromise, pokemonPromise, seriesPromise]).then(() =>
      setIsLoaded(true),
    );
  }, []);

  if (!isLoaded) {
    return <div>Loading</div>;
  }

  return (
    <SelectedCountProvider>
      <div className="base">
        <ScrollToTop />
        <HeaderBar
          filterType={selectType}
          setPokedata={setPokedata}
          setIsLoaded={setIsLoaded}
        />
        <PokemonDisplay
          pokedata={pokedata}
        />
      </div>
    </SelectedCountProvider>
  );
}
