import React, { createContext, useContext, useState } from "react";

const SelectedCountContext = createContext<{
  selectedCount: number;
  filterConnected: boolean;
  setSelectedCount: React.Dispatch<React.SetStateAction<number>>;
  setFilterConnected: React.Dispatch<React.SetStateAction<boolean>>;
} | null>(null);

export const SelectedCountProvider = ({
  children,
}: {
  children: React.ReactNode;
}) => {
  const [selectedCount, setSelectedCount] = useState(0);
  const [filterConnected, setFilterConnected] = useState(false);
  return (
    <SelectedCountContext.Provider
      value={{
        selectedCount,
        setSelectedCount,
        filterConnected,
        setFilterConnected,
      }}
    >
      {children}
    </SelectedCountContext.Provider>
  );
};

export const useSelectedCount = () => {
  const context = useContext(SelectedCountContext);
  if (!context) {
    throw new Error(
      "useSelectedCount must be used within a SelectedCountProvider",
    );
  }
  return context;
};
