import React, { StrictMode } from "react";
import { createRoot } from "react-dom/client";
import "./styles.css";

import App from "./App";

const rootEle = document.getElementById("root");
if (rootEle) {
  const root = createRoot(rootEle);
  const urlSelectType = Object.fromEntries(
    location.search
      .replace("?", "")
      .split("&")
      .map((i) => i.split("=")),
  );
  const select_type = urlSelectType["select_type"] || "series";
  root.render(
    <StrictMode>
      <App selectType={select_type} />
    </StrictMode>,
  );
} else {
  console.warn("Missing Root ID");
}
